import * as utils from '../../../core/utils.js';

import { mapState } from "vuex";

export default {
    name: "about",
    data() {
        return {
            is_mall: false,
            is_scan_code_pay: false,
            is_not_wechat: false,
        };
    },
    computed: {
        ...mapState({
            clerkImg: state => state.mallConfig.__wxapp_img.clerk,
            mall: state => state.mallConfig.mall
        }),
    },
    onLoad() { this.$commonLoad.onload();



        this.getList();
    },
    methods: {
        toStatic() {
            uni.navigateTo({
                url: '/plugins/clerk/statics/statics'
            });
        },
        toOrder() {
            uni.navigateTo({
                url: '/plugins/clerk/order/order?status=1'
            });
        },
        toCard() {
            uni.navigateTo({
                url: '/plugins/clerk/order/order?status=2'
            });
        },
        clerk() {
            let that = this;

            uni.scanCode({
                success(res) {
                    if(res.path) {
                        let path = res.path
                        path = path.split('?');
                        let scene = path[1];
                        let reg = new RegExp("scene=", "g");
                        scene = path[1].replace(reg, "");
                        that.$request({
                            url: that.$api.clerk.qrcode_parameter,
                            data: {
                                token: scene
                            }
                        }).then(response => {
                            if (response.code === 0) {
                                let detail = response.data.detail;
                                let url = '/' + detail.path;
                                if (detail.data) {
                                    url += '?' + utils.objectToUrlParams(detail.data);
                                }
                                uni.redirectTo({
                                    url: url
                                })
                                uni.options = null;
                            }
                        });
                    }else {
                        uni.showModal({
                            title: '提示',
                            content: '暂不支持此二维码，请使用应用内扫一扫功能',
                            showCancel: false,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }
            })







































































        },
        scan() {
            uni.redirectTo({
                url: '/pages/app_admin/payment-code/payment-code?is_clerk_enter=1'
            })
        },
        getList() {
            let that = this;
            that.$request({
                url: that.$api.clerk.info,
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.is_mall = response.data.is_mall;
                    that.is_scan_code_pay = response.data.is_clerk;
                }else {
                    if(response.msg == '无核销信息权限') {
                        uni.showModal({
                            title: '提示',
                            content: '该帐号无核销权限',
                            showCancel: false,
                            success: function (res) {
                                uni.redirectTo({
                                    url: '/pages/index/index'
                                })
                            }
                        });
                    }else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
    }
}
